/**
 * App
 */

import 'Utils/closest.polyfill.js';
import 'Utils/matches.polyfill.js';

import navbar from 'Components/navbar';

document.addEventListener('DOMContentLoaded', function () {
    navbar();


    /**
     * Carousel
     * @type {HTMLCollectionOf<Element>}
     */
    let images_dots = document.getElementsByClassName("carousel__images-dots__item"),
        dots = document.getElementsByClassName("carousel__dots--item"),
        carousel_wrapper = document.getElementsByClassName("carousel"),
        carousel__items = document.getElementsByClassName("carousel__item");

    var carousel = function () {
        let index = this.getAttribute("data-carousel-index");
        carousel_wrapper[0].setAttribute("data-carousel-index", index)

        /*
          set carousel__item && dots statements
         */
        for (var i = 0; i < carousel__items.length; i++) {
            let carousel_item_index = carousel__items[i].getAttribute("data-carousel-index"),
                carousel__item = carousel__items[i];

            if (carousel__item.classList.contains("is--active") && carousel_item_index != index) {
                carousel__item.classList.add("is--fadeout");
                setTimeout(function(){
                    carousel__item.classList.remove("is--active");
                    carousel__item.classList.remove("is--fadeout");
                }, 1200)
                images_dots[i].classList.remove("is--active");
                dots[i].classList.remove("is--active");
            } else if (!carousel__item.classList.contains("is--active") && carousel_item_index == index) {
                carousel__item.classList.add("is--active");
                images_dots[i].classList.add("is--active");
                dots[i].classList.add("is--active");
            }
        }

    };

    for (var i = 0; i < images_dots.length; i++) {
        images_dots[i].addEventListener('click', carousel, false);
        dots[i].addEventListener('click', carousel, false);
    }


    /**
     * Mobile menu
     */

    let navbar__titles = document.getElementsByClassName("navbar__title");
    for (var n = 0; n < navbar__titles.length; n++) {
        navbar__titles[n].addEventListener('click', function(){
            let siblings = this.parentNode.parentNode.childNodes,
                current__class = this.parentNode.classList.contains('is--active'),
                current_navbar = this.parentNode;
            siblings.forEach(sib => {
                if( typeof sib.classList == 'object'  ) {
                    if( sib === current_navbar ){
                        sib.classList.toggle('is--active');
                        sib.classList.remove('is--hidden');
                    } else {
                        if( current__class === true ) {
                            sib.classList.remove('is--hidden');
                        } else {
                            sib.classList.add('is--hidden');
                        }
                        sib.classList.remove('is--active');

                    }
                }

            })
        });
    }
});
